/* Estilos globales */
body {
  font-family: 'Inter', sans-serif;
  background-color: #161616;
  color: white;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.vaccine-tracker-container {
  max-width: 720px;
  margin: 0 auto;
  padding: 20px;
  background-color: #161616;
}

.vaccines-container {
  display: flex;
  flex-direction: column;
  margin-top: 16px;
}

/* Variantes de texto */

.p-bold, .p-gray, .p-white {
  margin: 0;
}

.p-bold {
  font-size: 14px;
  font-weight: bold;
  line-height: auto;
  color: white;
}

.p-gray {
  font-size: 14px;
  font-weight: normal;
  line-height: auto;
  color: #A0A0A0;
}

.p-white {
  font-size: 14px;
  font-weight: normal;
  line-height: auto;
  color: white;
}

/* Estilos existentes */
h1 {
  text-align: center;
  color: #4a6da7;
  margin-bottom: 30px;
}

h2 {
  color: #4a6da7;
  border-bottom: 2px solid #eaeaea;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.vaccine-form, .vaccine-list {
  background-color: #161616;
  border-radius: 10px;
}

.vaccine-list .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #282828;
  padding: 12px;
}

.date-fields {
  display: flex;
  flex-direction: row;
  gap: 16px;
}

.form-group {
  margin-bottom: 20px;
  width: 100%; /* Asegúrate de que el contenedor del input tenga un ancho del 100% */
}

label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #555;
}

input[type="text"],
input[type="date"],
input[type="file"] {
  font-family: 'Inter', sans-serif;
  margin-top: 4px;
  color: #757575;
  background-color: #161616;
  width: 100%; /* Asegúrate de que los inputs tengan un ancho del 100% */
  padding: 12px;
  border: 1px solid #343434;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.3s;
  box-sizing: border-box; /* Asegúrate de que el padding y el borde se incluyan en el ancho total */
}

input[type="text"]:focus,
input[type="date"]:focus {
  color: white;
  border-color: #4a6da7;
  outline: none;
  box-shadow: 0 0 0 2px rgba(74, 109, 167, 0.2);
}

button {
  background-color: #4a6da7;
  color: white;
  border: none;
  padding: 14px 28px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  transition: background-color 0.3s, transform 0.2s;
  display: block;
  margin: 20px auto 0;
}

button:hover {
  background-color: #3a5d97;
  transform: translateY(-2px);
}

button:active {
  transform: translateY(0);
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
  transform: none;
}

.image-preview {
  margin: 20px 0;
  text-align: center;
}

.image-preview h3 {
  margin-bottom: 10px;
  font-size: 16px;
  color: #666;
}

.image-preview img {
  max-width: 100%;
  max-height: 300px;
  border: 1px solid #eee;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.vaccine-card h3 {
  color: #4a6da7;
  margin-top: 0;
  margin-bottom: 15px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}

.vaccine-image img {
  width: 100%;
  border-radius: 8px;
  margin-top: 15px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.reminder {
  background-color: #e7f3fe;
  padding: 10px;
  border-radius: 6px;
  margin: 15px 0;
  border-left: 4px solid #4a6da7;
}

.reminder.overdue {
  background-color: #fff0f0;
  border-left: 4px solid #ff6666;
}

@media (max-width: 768px) {
  .vaccines-container {
    grid-template-columns: 1fr;
  }
  
  .vaccine-form, .vaccine-list {
    padding: 20px 0px;
  }
}