.vaccine-card {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #161616;
    border-radius: 10px;
    padding: 12px;
  }
  
  .vaccine-card:hover {
    background-color: #232323;
  }
  
  .vaccine-card h3 {
    color: #4a6da7;
    margin-top: 0;
    margin-bottom: 15px;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
  }
  
  .vaccine-image img {
    width: 100%;
    border-radius: 8px;
    margin-top: 15px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  }

  .vaccine-info {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
  .vaccine-info-right {
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: flex-end;
  }

  .p-bold {
    font-size: 14px;
    font-weight: bold;
    line-height: auto;
    color: white;
  }
  
  .p-gray {
    font-size: 14px;
    font-weight: normal;
    line-height: auto;
    color: #A0A0A0;
  }
  
  .p-white {
    font-size: 14px;
    font-weight: normal;
    line-height: auto;
    color: white;
  }
  